.Grid-item {
  position: relative;
  -webkit-transition: color .3s, background-color .3s;
  transition: color .3s, background-color .3s;
  background-size: cover;
  background-position: center;
  min-height: 8em;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.Grid-item--exhibit {
  background-color: white;
}

.Grid-item--exhibit:hover .Grid-item-inner {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.Grid-item--exhibit:hover .stroke-text--thin {
  -webkit-text-fill-color: unset;
  -webkit-text-stroke-width: unset;
}

.Grid-item--exhibit:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #FAEEED;
  opacity: .6;
  top: 0;
  left: 0;
  pointer-events: none;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}

.Grid-item--exhibit:hover:before {
  opacity: .8;
}
.Grid-item--exhibit .Grid-item-inner {
  opacity: 1;
}
.Grid-item--exhibit:hover .Grid-item-inner {
  opacity: 1;
}

.Grid-item--fillTile {
  padding: 2em;
}

.Grid-item--fillTile .Grid-item-inner {
  font-size: 14px;
}

@media (min-width: 800px) {
  .Grid-item--fillTile {
    padding: 3em;
  }

  .Grid-item--fillTile .Grid-item-inner {
    font-size: 22px;
  }
}

.Grid-item-inner {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  -webkit-transition: opacity .3s, -webkit-transform .5s;
  transition: opacity .3s, -webkit-transform .5s;
  transition: opacity .3s, transform .5s;
  transition: opacity .3s, transform .5s, -webkit-transform .5s;
}

a.Grid-item-inner { padding: 1rem; }

.Grid-item-inner .size--body { padding: 0 1rem; }

.Grid-item-inner .size--headerxl {
  line-height: 1;
}
.Grid-item--currentExhibit {
  pointer-events: none;
}

/*grid */
@media (min-width: 376px) {
  .Grid-list {
    display: grid;
    list-style-type: none;
    overflow: hidden;
    grid-template-columns: repeat(2, 1fr);
  }
  .Grid-item-inner:before {
    content: '';
    padding-bottom: 100%;
  }
}
@media (min-width: 800px) {
  .Grid-list {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1000px) {
  .Grid-list {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1400px) {
  .Grid-list {
    grid-template-columns: repeat(5, 1fr);
  }
}



